	let sex = ['不限', '男', '女'];
	let qg = ['秘密', '未婚', '已婚'];
	let job = ['秘密', 'IT', '作家'];
	
	import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue'
	
	export default {
		components: {
			mpvueCityPicker
		},
		data() {
			return {
				userpic:"../../static/demo/demo6.jpg",
				userName: "闻说",
				sex: "不限",
				qg: "未婚",
				job: "IT",
				birthday: "",
				// 三级城市联动
				cityPickerValueDefault: [0, 0, 1],
				pickerText: '未知'
			}
		},
		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			}
    },
		onBackPress() {
		  if (this.$refs.mpvueCityPicker.showPicker) {
		  	this.$refs.mpvueCityPicker.pickerCancel();
		    return true;
		  }
		},
		onUnload() {
			if (this.$refs.mpvueCityPicker.showPicker) {
				this.$refs.mpvueCityPicker.pickerCancel()
			}
		},
		methods: {
			// 修改头像
			changeImg () {
				uni.chooseImage({
					count: 1, // 图片数量的限制
					sizeType: ['compressed'], // 图片压缩
					success: (res) => {
						this.uerpic = res.tempFilePaths;
					}
				});
			},
			// 单列选项
			changeOne (val) {
				let arr = [];
				switch (val){
					case 'sex':
						arr = sex;
						break;
					case 'qg':
						arr = qg;
						break;
					case 'job':
						arr = job;
						break;
				}
				
				uni.showActionSheet({
					itemList: arr,
					success: res => {
						switch (val){
							case 'sex':
								this.sex = arr[res.tapIndex];
								break;
							case 'qg':
								this.qg = arr[res.tapIndex];
								break;
							case 'job':
								this.job = arr[res.tapIndex];
								break;
							default:
								break;
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			// 修改生日
			bindDateChange (e) {
				this.birthday = e.target.value
			},
			// 获取时间
			getDate (type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
						year = year - 60;
				} else if (type === 'end') {
						year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			// 三级联动选择
			showMulLinkageThreePicker() {
				this.$refs.mpvueCityPicker.show()
			},
			onConfirm(e) {
				this.pickerText = e.label;
			},
			// 提交
			submit () {
				
			}
		}
	}
